<template>
  <div class="about">
    <h1></h1>
    <el-form label-width="120px" @submit.native.prevent="saveform('model')" :model="model" :rules="rules" ref="model">
      <el-form-item label=":" prop="username">
        <el-col :span="4">
          <el-input v-model="model.username" clearable></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label=":" prop="newpassword">
        <el-col :span="4">
          <el-input v-model="model.newpassword" type="password" clearable autocomplete="off"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label=":" prop="password">
        <el-col :span="4">
          <el-input v-model="model.password" type="password" clearable autocomplete="off"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label=":">
        <el-col :span="4">
          <el-input v-model="model.name" clearable></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label=":">
        <el-col :span="4">
          <el-input v-model="model.email" clearable></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label=":">
        <el-col :span="4">
          <el-input v-model="model.phone" clearable></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label=":">
        <el-col :span="4">
          <el-input v-model="model.card" clearable></el-input>
        </el-col>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>



<script>
  export default {
    props: {
      id: {}
    },
    data() {
      const vaildatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.model.password !== '') {
            this.$refs.model.validateField('checkpassword');
          }
          callback();
        }
      };
      const vaildatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.model.newpassword) {
          callback(new Error(''));
        } else {
          callback();
        }
      }
      return {
        model: {},
        rules: {
          name: [{
              required: true,
              message: '',
              trigger: 'blur'
            },
            {
              min: 1,
              max: 5,
              message: ' 1  5 ',
              trigger: 'blur'
            }
          ],
          newpassword: [{
            validator: vaildatePass,
            trigger: 'blur'
          }, {
            required: true,
            message: '',
            trigger: 'blur'
          }],
          password: [{
            validator: vaildatePass2,
            trigger: 'blur'
          }, {
            required: true,
            message: '',
            trigger: 'blur'
          }]
        }
      }
    },
    methods: {
      saveform(model) {
        this.$refs[model].validate(valid => {
          if (valid) {
            this.save();
          } else {
            this.$message({
              type: 'error',
              message: ''
            })
            return false
          }
        })
      },

      async save() {
        let res = this.$http.post('reader/readers', this.model)
        res = await this.$http.get('reader/readers')
        this.$router.push('/reader/list')
        this.$message({
          type: 'success',
          message: ''
        })
      },
    },

  }
</script>