<template>
  <div class="about">
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="350"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <el-button type="success" size="small" @click="$router.push(`/ads/edit/${scope.row._id}`)" icon="el-icon-edit">
          </el-button>
          <el-button type="danger" size="small" @click="remove(scope.row)" icon="el-icon-delete"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        items: []
      }
    },
    methods: {
      async fetch() {
        const res = await this.$http.get('ad/ads')
        this.items = res.data
      },
      async remove(row) {
        this.$confirm(`: "${row.name}"`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          //
          const res = await this.$http.delete(`ad/ads/${row._id}`)

          this.$message({
            type: 'success',
            message: '!'
          });
          //
          this.fetch()
        });
      }
    },
    created() {
      this.fetch()
    }
  }
</script>