<template>
  <div class="about">
    <h1> </h1>
    <el-form label-width="120px" @submit.native.prevent="saveform('model')" :model="model" :rules="rules" ref="model" >
      <el-form-item label="" prop="username">
        <el-col :span="6">
          <el-input v-model="model.username" prop="username"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-col :span="6">
          <el-input type="password" v-model="model.password"  ></el-input>
        </el-col>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        model: {},
        rules: {
          username: [{
              required: true,
              message: '',
              trigger: 'blur'
            },
            {
              min: 1,
              max: 16,
              message: ' 1  16 ',
              trigger: 'blur'
            }
          ],
          password: [{
              required: true,
              message: '',
              trigger: 'blur'
            },
            
          ],
        }
      }
    },
    methods: {
      saveform(model) {
        this.$refs[model].validate(valid => {
          if (valid) {
            this.save();
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      async save() {
        const res = this.$http.post('admin/admin_users', this.model)
        if(!res){
          return false
        }
        this.$http.get('admin/admin_users')
        this.$router.push('/admin_users/list')
        this.$message({
          type: 'success',
          message: ''
        })
      },
    },
  }
</script>