<template>
  <div class="about">
    <div>
      <h1></h1>
    </div>
    <el-table :data="items.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))">
      <el-table-column prop="_id" label=""></el-table-column>
      <el-table-column prop="username" label=""></el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        items: [],
      }
    },
    methods: {
      async fetch() {
        const res = await this.$http.get('admin/admin_users')
        this.items = res.data
      },
      async remove(row) {
        this.$confirm(`"${row.username}"`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          //
          const res = await this.$http.delete(`admin/admin_users/${row._id}`)

          this.$message({
            type: 'success',
            message: '!'
          });
          //
          this.fetch()
        });
      }
    },
    created() {
      this.fetch()
    }
  }
</script>