<template>
  <div class="about">
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="createdAt" label="">
        <template slot-scope="scope">{{ scope.row.createdAt | data }}</template>
      </el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <el-button type="success" size="small" @click="$router.push(`/server/edit/${scope.row._id}`)"
            icon="el-icon-edit">
          </el-button>
          <el-button type="danger" size="small" @click="remove(scope.row)" icon="el-icon-delete"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import dayjs from 'dayjs'
  export default {
    filters: {
      data(val) {
        return dayjs(val).format('YYYY/MM/DD HH:mm')
      }
    },
    data() {
      return {
        items: []
      }
    },
    methods: {
      async fetch() {
        const res = await this.$http.get('server/servers')
        this.items = res.data
      },
      async remove(row) {
        this.$confirm(` "${row.name}"`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          //
          const res = await this.$http.delete(`server/servers/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          });
          //
          this.fetch()
        });
      }
    },
    created() {
      this.fetch()
    }
  }
</script>